@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.i {
    border: 1px solid red;
}

.container {
    --color-bg: #dac18e;
    --color-blue: #0080cc;
    --color-whiteblue: #92c9da;
    --color-darkblue: #dac18e;
    --color-darkblue1: #184a64;
    --color-lightblue: #da9728;
    --color-pink: #ff6cc5;
    --color-darkpink: #EC61B5;
    --color-purple: #ff6cc5;
    --color-lilac: #ac9bfb;
    --color-red: #d36d71;
    --color-green: #8BC24A;
    --color-black: #333;
    --color-white: rgba(255, 255, 255, .7);
    --box-shadow-black: 1px 2px 3px rgba(0, 0, 0, .4);
    --box-shadow-white: 1px 2px 3px rgba(255, 255, 255, .3);
    --text-shadow-black: 1px 1px 1px rgba(0, 0, 0, .4);
    --text-shadow-white: 1px 1px 1px rgba(255, 255, 255, .3);
}

body {
    background: #8c1b13; /* Old browsers */
    /*background: -moz-linear-gradient(135deg, #262856 2%, #8c1b13 20%); !* FF3.6-15 *!*/
    /*background: -webkit-linear-gradient(135deg, #262856 2%, #8c1b13 20%); !* Chrome10-25,Safari5.1-6 *!*/
    /*background: linear-gradient(135deg, #262856 2%, #8c1b13 20%); !* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ *!*/
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#262856', endColorstr='#274f8a', GradientType=1); /* IE6-9 fallback on horizontal gradient */
    font: 300 15px/28px "Montserrat", sans-serif;
    color: white;
    font-weight: 500;
    letter-spacing: 0;
    background-attachment: fixed;
}

a {
    color: #fff;
}

a:hover {
    color: #0bcade;
    text-decoration: none;
}

.form-group {
}

.form-group label {
    margin-bottom: 2px;
    margin-left: 1px;
}

.form-help {
    margin-top: 5px;
    line-height: 16px;
    color: #DDD;
    font-size: 10pt;
}

.container {
    margin-top: 1%;
    margin-bottom: 3%;
}

.nav-wrapper {
    text-align: right;
}

.nav {
    list-style: none;
    vertical-align: middle;
    display: inline-block;
    margin: 10px 0px 10px 0;
}

.nav > li {
    float: left;
    margin-left: 5px;
    position: relative;
}

.nav > li > a {
    padding: 5px;
    color: #FFF;
    font-size: 12pt;
    font-weight: 600;
}

.nav li > a:hover {
    text-decoration: underline;
}

.nav > li ul {
    display: none;
    position: absolute;
    list-style: none;
    padding: 0;
    z-index: 2;
    text-align: left;
    box-shadow: 1px 2px 4px var(--box-shadow-black);
    border-radius: 5px;
}

.nav > li:hover ul {
    display: block;
}

.nav > li ul li {
}

.nav > li ul li > a {
    color: #333;
    font-weight: 400;
    display: block;
    white-space: nowrap;
    padding: 5px 10px;
    border-bottom: 1px solid #CCC;
    border-top: 1px solid #FFF;
    overflow: hidden;
    background: #EEE;
}

.nav > li ul li > a:hover {
    background: #F9F9F9;
    text-decoration: none;
}

.nav > li ul li:first-child > a {
    border-top: 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.nav > li ul li:last-child > a {
    border-bottom: 0;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.section-left {
    margin-bottom: 25px;
}

.logotype-wrapper {
    text-align: center;
    background-image: url(../../static/media/bg.4e51dbf7.png);
    background-size: 100% 100%;
    height: 300px;
}

.logotype {
    position: relative;
}

.logotype img {
    width: 150%;
    margin-top: 20px;
    margin-bottom: 15px;
    outline: 0;
    opacity: .9;
}

.logotype a {
    outline: none;
}

.logotypeX3,
.logotypeX4 {
    text-align: left;
}

.logotypeX3 img,
.logotypeX4 img {
    max-width: 250px;
}

.lang {
}

.border-gradient {
    position: relative;
    border: 1px solid #dac18e;
    border-radius: 9px;
    box-shadow: inset 1.5px 1.5px 1.5px rgba(0, 0, 0, .25);
}

.border-gradient_content {
