.binary-wrapper,
.ternary-wrapper {
    overflow: hidden;
    text-align: center;
}

.binary,
.ternary {
    margin: 25px 15px 25px 0;
    width: 110px;
    text-align: center;
    vertical-align: top;
    color: #EBE1FF;
    display: inline-block;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.binary-root,
.ternary-root {
    position: relative;
    z-index: 2;
    width: 90%;
    margin: 0 auto;
    border-radius: 6px;
    padding: 7px;
    font-weight: bold;
    display: block;
    box-shadow: 0px 2px 5px #4E3AA7;
}

.ternary {
}

.ternary-root {
}

.ternary-children {
    margin-top: 20px;
}

.ternary-children > div {
    position: relative;
    border-radius: 50%;
    display: inline-block;
    width: 20px;
    height: 20px;
    z-index: 2;
}

.ternary-children > div a {
    padding: 5px;
    display: inline-block;
    width: inherit;
    height: inherit;
    position: absolute;
    left: 0;
    top: 0;
}

.ternary-children div:nth-child(2) {
    margin: auto 15px;
}

.ternary-branchs {
    margin: 0 auto;
    position: relative;
    width: 1px;
    height: 1px;
}

.ternary-branchs > div {
    border-left: 1px dashed rgba(255, 255, 255, 0.5);
    width: 1px;
    height: 25px;
    position: absolute;
    z-index: 1;
}

.ternary-branchs > div:nth-child(1) {
    top: -50px;
    left: -27px;
    transform: rotate(36deg);
}

.ternary-branchs > div:nth-child(2) {
    top: -53px;
    right: -1px;
}

