    border-bottom: 1px solid #FEFEFE;
    padding: 7px 0;
}
.alert-socket__items > li:first-child {
    border: 0;
}
.alert-socket__img {
    margin-right: 5px;
    margin-bottom: 1px;
    color: #333;
    height: 20px;
    width: 20px;
}
.alert-socket__items .elapsedTime {
    color: #888;
    text-align: right;
}
.alert-socket__cell {
    position: absolute;
    right: 55px;
    bottom: 25px;
    padding: 8px 15px;
    background: #333;
    width: 250px;
    border-radius: 9px;
    border-bottom-right-radius: 0;
    display: none;
    line-height: 18px;
    background-color: #F6F6F6;
    border: 2px solid #DDDDDD;
    color: #676767;
}
