    z-index: 3;
    background-color: #00b0c2;
    font-size: 18pt;
    display: block;
    border-radius: 50%;
    color: white;
    width: 55px;
    height: 55px;
    padding-top: 14px;
    padding-left: 17px;
    cursor: pointer;
    transition: 500ms;
}
.alert-socket__btn:hover {
    background-color: #277FAE;
}
.alert-socket__subject {
    text-align: center;
    margin: 0;
    text-transform: uppercase;
    text-decoration: underline;
}

.pulse {
  overflow: visible;
  position: relative;
}
.pulse::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: inherit;
  border-radius: inherit;
  transition: opacity .3s, transform .3s;
  -webkit-animation: pulse-animation 1s cubic-bezier(0.24, 0, 0.38, 1) infinite;
          animation: pulse-animation 1s cubic-bezier(0.24, 0, 0.38, 1) infinite;
  z-index: -1;
}
@-webkit-keyframes pulse-animation {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0;
    transform: scale(1.5);
  }
  100% {
    opacity: 0;
    transform: scale(1.5);
  }
}
@keyframes pulse-animation {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0;
    transform: scale(1.5);
  }
  100% {
    opacity: 0;
    transform: scale(1.5);
  }
}

